  //     frame.innerHTML="<h5>" + title + "</BR>" + this.computeDuration(current - this.startTime) + "</h5>";
  //     frame.appendChild(img).setAttribute("style", "width: 200px;");
  //     // 親タグに追加
  //     this.parentElm.appendChild(frame);
  //   }
