// 画質を調べる
function quality(data, w, h) {
  var n = data.length;

  var gray = [];
  var ww = Math.floor(w / 4);

  for (var i = 0; i < n; i += 4) {
    var R = data[i];
    var G = data[i + 1];
    var B = data[i + 2];
    var A = data[i + 3];
    gray.push((0.298912 * R + 0.586611 * G + 0.114478 * B) * A / 255 | 0);
  }

  var a0 = new Array();
  for (var i = 0; i < h; i++) {
    for (var j = 0; j < ww; j++) {
      var v = i * w + j;
      a0.push(gray[v]);
    }
  }

  var a1 = new Array();
  for (var i = 0; i < h; i++) {
    for (var j = ww; j < 2 * ww; j++) {
      var v = i * w + j;
      a1.push(gray[v]);
    }
  }

  var a2 = new Array();
  for (var i = 0; i < h; i++) {
    for (var j = 2 * ww; j < 3 * ww; j++) {
      var v = i * w + j;
      a2.push(gray[v]);
    }
  }

  var a3 = new Array();
  for (var i = 0; i < h; i++) {
    for (var j = 3 * ww; j < w; j++) {
      var v = i * w + j;
      a3.push(gray[v]);
    }
  }

  var v0 = standard_deviation(a0);
  var v1 = standard_deviation(a1);
  var v2 = standard_deviation(a2);
  var v3 = standard_deviation(a3);

console.log("v0 " + JSON.stringify(v0));
console.log("v1 " + JSON.stringify(v1));
console.log("v2 " + JSON.stringify(v2));
console.log("v3 " + JSON.stringify(v3));
}

//実数を有効桁で丸目
function marume(a, n) {
  const p = Math.pow(10, n);
  return Math.round(a * p) / p;
}

/*
 * 平均を求める
 */
function average(data) {
  let sum = 0; // sawano
  for (let i = 0; i < data.length; i++) { // sawano
    sum = sum + data[i];
  }
  return (sum / data.length);
}

/*
 * 分散を求める
 * 分散 ＝（（データ－平均値）の２乗）の総和 ÷ 個数
 */
function variance(data) {
  // 平均値を求める
  var ave = average(data);

  var varia = 0;
  for (let i = 0; i < data.length; i++) {  // sawano
    varia = varia + Math.pow(data[i] - ave, 2);
  }
  return {ave: ave, vari: (varia / data.length)};
}

/*
 * 標準偏差を求める
 */
//戻り値　v.ave：平均値、v.vari：分散、v.sd：標準偏差、v.coe：変動係数
function standard_deviation(data) {
//console.log(data.length);
  // 分散を求める
  var v = variance(data);
  var sd = Math.sqrt(v.vari);
  var coe = sd / v.ave;

  // 分散の平方根
  return {ave: v.ave, vari: v.vari, sd: sd, coe: coe};
}

/*
  2点を通る直線（y = ax + b）を求める
  戻り値は、直線の係数a、b
*/
function lineExp(x0, y0, x1, y1) {
  var m = {};

  var dx = x1 - x0;
  var dy = y1 - y0;

  if (dx == 0) {
    m.a = 0;
    m.b = 0;
    m.dist = 0;
    return m;
  }
  else {
    m.a = dy / dx;
    m.b = (x1 * y0 - x0 * y1) / dx;
  }

  m.dist = Math.sqrt(dx * dx + dy * dy);

  return m;
}

/*
  線分と点の垂線の距離を知るにはどうしたらい
    https://detail.chiebukuro.yahoo.co.jp/qa/question_detail/q1195497616
*/
function distance2(dotX, dotY, lineX1, lineY1, lineX2, lineY2) {
// ax + by + c = 0
  var a = lineY1 - lineY2;
  var b = lineX2 - lineX1;
  var c = (- b * lineY1) + (- a * lineX1);
  var root = Math.sqrt(a * a + b * b);

  if (root == 0.0) {
    return -1.0; /* 求められない場合は負の値を返す */
  }

// 求める距離
  var dist = ((a * dotX) + (b * dotY) + c) / root;

  if (dist < 0.0) {
    dist = -dist;
  }

  return Math.round(dist);
}

//ベクトルの長さを計算する
function get_vector_length(v) {
  return Math.pow((v.x * v.x) + (v.y * v.y), 0.5);
}

//ベクトル内積
function dot_product(vl, vr) {
  return vl.x * vr.x + vl.y * vr.y;
}

/*
  ２つのベクトルABのなす角度θを求める
    http://www.sousakuba.com/Programming/gs_two_vector_angle.html
*/
function AngleOf2Vector(a, b) {
  var A = {"x": a.x2 - a.x1, "y": a.y2 - a.y1};
  var B = {"x": b.x2 - b.x1, "y": b.y2 - b.y1};
//※ベクトルの長さが0だと答えが出ませんので注意してください。

//ベクトルAとBの長さを計算する
  var length_A = get_vector_length(A);
  var length_B = get_vector_length(B);

//内積とベクトル長さを使ってcosθを求める
  var cos_sita = dot_product(A, B) / (length_A * length_B);

//cosθからθを求める
  var sita = Math.acos(cos_sita);

//ラジアンでなく0～180の角度でほしい場合はコメント外す
  sita = sita * 180.0 / Math.PI;

  return Math.round(sita);
}

/*
  ２つの線分a、線分bの位置関係（include, overlap, out）を求める
*/
function checkSegment(a, b) {
  var pointA = {x: a.x1, y: a.y1};
  var pointB = {x: a.x2, y: a.y2};
  var pointC = {x: b.x1, y: b.y1};
  var pointD = {x: b.x2, y: b.y2};
  var np1 = nearest(pointC, pointA, pointB);
  var np2 = nearest(pointD, pointA, pointB);
  var type = 'overlap';
  if ((np1.flag >= 0 && np1.flag <= 1) && (np2.flag >= 0 && np2.flag <= 1)) type = 'include';//remove C-D
  else if ((np1.flag <= 0 && np2.flag >= 1) || (np1.flag >= 1 && np2.flag <= 0)) type = 'Include';//remove A-B
  else if (np1.flag < 0 && np2.flag < 0) type = 'out';
  else if (np1.flag > 1 && np2.flag > 1) type = 'out';
  var ang = AngleOf2Vector(a, b);
  var near = false;
  if (np1.dist <= 5 && np2.dist <= 5) near = true;
  else if (type == 'include' || type == 'Include') {
    if (np1.dist <= 26 && np2.dist <= 26) near = true;
  }
  else if (type == 'overlap' || type == 'out') {
    if (np1.dist <= 8 && np2.dist <= 8) {
//console.log('OUT', np1.flag, np2.flag);
      if ((np1.flag > -2 && np1.flag < 2) && (np2.flag > -2 && np2.flag < 2)) near = true;
    }
  }
  var c = {type: type, near: near, ang: ang, dist1: np1.dist, dist2: np2.dist, flag1: np1.flag, flag2: np2.flag};
  return c;
}

/**
 * 任意の点から対象の直線上で最も近い点を取得する
 *
 * @param p0 任意の点
 * @param p1 直線の始点
 * @param p2 直線の終点
 * @return p0 から最も近い直線 (p1, p2) 上の点
 *
 * @see http://www.fumiononaka.com/TechNotes/Flash/FN1104002.html
 */
function closestPoint(p0, p1, p2) {
  var ax = p0.x - p1.x;
  var ay = p0.y - p1.y;
  var bx = p2.x - p1.x;
  var by = p2.y - p1.y;

  var d = ax * bx + ay * by;
  var l = Math.sqrt(bx * bx + by * by);
  var n = d / l;

  bx *= n / l;
  by *= n / l;
  var x = p1.x + bx;
  var y = p1.y + by;

  var d = Math.sqrt((p0.x - x) * (p0.x - x) + (p0.y - y) * (p0.y - y));

  return { x: x, y: y, dist: d };
}

/*
  ２つの直線の交点の座標を求める
    https://qiita.com/kazukisugita/items/c79e8a47defa93337cc9
*/
function CalcIntersectionPoint(pointA, pointB, pointC, pointD) {
  const intersection = {x: 0, y: 0, dist: 9999};//線分の点と交点の最小距離
  const dBunbo = (pointB.x - pointA.x) * (pointD.y - pointC.y) - (pointB.y - pointA.y) * (pointD.x - pointC.x);
//console.log('dBunbo', dBunbo);

  if (0 == dBunbo) {// 平行
    return intersection;
  }

  var vectorAC = {};
  vectorAC.x = pointC.x - pointA.x;
  vectorAC.y = pointC.y - pointA.y;

  var r = ((pointD.y - pointC.y) * vectorAC.x - (pointD.x - pointC.x) * vectorAC.y) / dBunbo;
  var s = ((pointB.y - pointA.y) * vectorAC.x - (pointB.x - pointA.x) * vectorAC.y) / dBunbo;

  var distance = {};
  distance.x = (pointB.x - pointA.x) * r;
  distance.y = (pointB.y - pointA.y) * r;

  intersection.x = Math.round(pointA.x + distance.x);
  intersection.y = Math.round(pointA.y + distance.y);

  var d = new Array(4);
  d[0] = Math.sqrt((intersection.x - pointA.x) * (intersection.x - pointA.x) + (intersection.y - pointA.y) * (intersection.y - pointA.y));
  d[1] = Math.sqrt((intersection.x - pointB.x) * (intersection.x - pointB.x) + (intersection.y - pointB.y) * (intersection.y - pointB.y));
  d[2] = Math.sqrt((intersection.x - pointC.x) * (intersection.x - pointC.x) + (intersection.y - pointC.y) * (intersection.y - pointC.y));
  d[3] = Math.sqrt((intersection.x - pointD.x) * (intersection.x - pointD.x) + (intersection.y - pointD.y) * (intersection.y - pointD.y));
  intersection.d = d;
  intersection.dist = Math.min.apply(null, d);
//console.log(Math.round(d[0]), Math.round(d[1]), Math.round(d[2]), Math.round(d[3]));
var nn = 0;
for (var i = 0; i < 4; i++)
if (d[i] < 30) nn++
intersection.near = nn;

  var dd = new Array(4);
  dd[0] = Math.sqrt((pointA.x - pointB.x) * (pointA.x - pointB.x) + (pointA.y - pointB.y) * (pointA.y - pointB.y));
  dd[1] = Math.sqrt((pointB.x - pointC.x) * (pointB.x - pointC.x) + (pointB.y - pointC.y) * (pointB.y - pointC.y));
  dd[2] = Math.sqrt((pointC.x - pointD.x) * (pointC.x - pointD.x) + (pointC.y - pointD.y) * (pointC.y - pointD.y));
  dd[3] = Math.sqrt((pointD.x - pointA.x) * (pointD.x - pointA.x) + (pointD.y - pointA.y) * (pointD.y - pointA.y));
  dd[4] = Math.sqrt((pointA.x - pointC.x) * (pointA.x - pointC.x) + (pointA.y - pointC.y) * (pointA.y - pointC.y));
  dd[5] = Math.sqrt((pointB.x - pointD.x) * (pointB.x - pointD.x) + (pointB.y - pointD.y) * (pointB.y - pointD.y));
  intersection.neardist = Math.min.apply(null, dd);

var ang = AngleOf2Vector({x1: pointA.x, y1: pointA.y, x2: pointB.x, y2: pointB.y}, {x1: pointC.x, y1: pointC.y, x2: pointD.x, y2: pointD.y});
if (ang < 45) intersection.near = 0;
intersection.ang = ang;
  intersection.checkAB = checkInclude(pointA.x, pointA.y, pointB.x, pointB.y, intersection.x, intersection.y);
  intersection.checkCD = checkInclude(pointC.x, pointC.y, pointD.x, pointD.y, intersection.x, intersection.y);

  return intersection;
}

//線分a-bの間に交点cがあるか否かをチェックする
function checkInclude(ax, ay, bx, by, cx, cy) {
  if (ax < bx) {
    if (cx >= ax && cx <= bx) ;//return true;
    else return false;
  }
  else {
    if (cx >= bx && cx <= ax) ;//return true;
    else return false;
  }

  if (ay < by) {
    if (cy >= ay && cy <= by) return true;
    else return false;
  }
  else {
    if (cy >= by && cy <= ay) return true;
    else return false;
  }
}

/*
  線分上の最も近い点
    https://qiita.com/yuba/items/2f35d1e5e162f44fc1b3

  線分a-bとpからの垂線の交点を求める
  flag <= 0ならばAの外側に垂線の交点がある
  flag >= 1ならばBの外側に垂線の交点がある
  0 <= flag <= 1 ならば、線分a-bの中に垂線の交点がある
  0に近いとa寄りに、1に近いとb寄りに垂線の交点は位置する
*/
function nearest(P, A, B) {
  var a = {}, b = {};

  a.x = B.x - A.x;
  a.y = B.y - A.y;
  b.x = P.x - A.x;
  b.y = P.y - A.y;

  // 内積 ÷ |a|^2
  var r = (a.x * b.x + a.y * b.y) / (a.x * a.x + a.y * a.y);

  let x = A.x + r * a.x;  // sawano
  let y = A.y + r * a.y;  // sawano
  let d = Math.round(Math.sqrt((P.x - x) * (P.x - x) + (P.y - y) * (P.y - y))); // sawano

  return {x: x, y: y, dist: d, flag: r};
}

function transCoord(points, angle) {
  var len = points.length;

  var center = {x: 0, y: 0};
  for (var i = 0; i < len; i++) {
    center.x += points[i].x;
    center.y += points[i].y;
  }
  center.x = Math.round(center.x / len);
  center.y = Math.round(center.y / len);
//console.log(center.x, center.y);

  var rotate = MATRIX.rotate(angle);
  var matrix = MATRIX.multiplyArray([rotate]);

  var p = [];
  for (var i = 0; i < len; i++) {
    p[i] = {};
    p[i].x = points[i].x - center.x;
    p[i].y = points[i].y - center.y;
    p[i] = MATRIX.multiplyVec(matrix, p[i]);
    p[i].x += center.x;
    p[i].y += center.y;
  }
  return p;
}

