// pictTransMain から
function readImageData(img, maxSize) {
  let oWidth = img.naturalWidth;
  let oHeight = img.naturalHeight;
  let wWidth = oWidth;
  let wHeight = oHeight;
  // let lS = maxSize;
  // let maxWidth = maxSize;
  // let maxHeight = maxSize;
  // let cWidth, cHeight;

//   if (oWidth > oHeight) {
//     if (oWidth > lS) {
//       cWidth = lS;
//       cHeight = oHeight * lS / oWidth;
//     }
//     else {
//       cWidth = oWidth;
//       cHeight = oHeight;
//     }
//   }
//   else {
//     if (oHeight > lS) {
//       cWidth = oWidth * lS / oHeight;
//       cHeight = lS;
//     }
//     else {
//       cWidth = oWidth;
//       cHeight = oHeight;
//     }
//   }
// console.log(cWidth, cHeight);
  // JPEGの場合には、EXIFからOrientation（回転）情報を取得
  let orient = 1;
  if (img.src.split(',')[0].match('jpeg')) {　// toDo improve
    orient = getOrientation(img.src);
  }
  // JPEG以外や、JPEGでもEXIFが無い場合などには、標準の値に設定
  orient = orient || 1;
console.log('orientation', orient);

  // ９０度回転など、縦横が入れ替わる場合には事前に最大幅、高さを入れ替えておく  → maxWidth と maxHeight は 同じ値 意味ない？
  // if (orient > 4) {
  //   var tmpMaxWidth = maxWidth;
  //   maxWidth = maxHeight;
  //   maxHeight = tmpMaxWidth;
  // }

  if (wWidth > maxSize || wHeight > maxSize) {
    var ratio = wWidth / maxSize;
    if (ratio <= wHeight / maxSize) {
      ratio = wHeight / maxSize;
    }
    wWidth = Math.round(oWidth / ratio);
    wHeight = Math.round(oHeight / ratio);
  }

  return {orient, wWidth, wHeight};
}

