// EXIFのOrientation情報からCanvasを回転させておく
// pictTransMain の readImageData　と
// pitTransLoader の function transformCoordinate2(id0, id1, thisImage) { から
function transformOrient(img, lsize) { //canvas, tranSize) {
  let tranSize = readImageData(img, lsize);
  let orient = tranSize.orient;
  let wHeight = tranSize.wHeight;
  let wWidth = tranSize.wWidth;
  // let cHeight = tranSize.cHeight;
  // let cWidth = tranSize.cWidth;

  // let canvas = document.createElement("canvas");
  // let ctx = canvas.getContext('2d');
  // canvas.width = img.naturalWidth;
  // canvas.height = img.naturalHeight;
  // ctx.drawImage(img, 0, 0, img.naturalWidth, img.naturalHeight);  // もとさいず

  // console.log(orient, wWidth, wHeight);
  //   var canvas0 = document.getElementById(id0);
  //   if (!canvas0) {
  //     canvas0 = document.createElement('canvas');
  //     canvas0.id = id0;
  //     document.body.appendChild(canvas0);
  //   }
  // var canvas0 = document.createElement('canvas');  // canvas0は不用?
  // let ctx0 = canvas0.getContext('2d');
  // var canvas1 = document.getElementById(id1);
  // if (!canvas1) {
  let  canvas1 = document.createElement('canvas');
  //   canvas1.id = id1;
  //   document.body.appendChild(canvas1);
  // }
  let ctx1 = canvas1.getContext('2d');

  if (orient > 4) {
    // canvas0.width = cHeight;
    // canvas0.height = cWidth;
    canvas1.width = wHeight;
    canvas1.height = wWidth;
  } else {
    // canvas0.width = cWidth;
    // canvas0.height = cHeight;
    canvas1.width = wWidth;
    canvas1.height = wHeight;
  }

  switch (orient) {
    case 2:// horizontal flip
      // ctx0.translate(cWidth, 0);
      // ctx0.scale(-1, 1);
      ctx1.translate(wWidth, 0);
      ctx1.scale(-1, 1);
      break;
    case 3:// 180 rotate left
      // ctx0.translate(cWidth, cHeight);
      // ctx0.rotate(Math.PI);
      ctx1.translate(wWidth, wHeight);
      ctx1.rotate(Math.PI);
      break;
    case 4:// vertical flip
      // ctx0.translate(0, cHeight);
      // ctx0.scale(1, -1);
      ctx1.translate(0, wHeight);
      ctx1.scale(1, -1);
      break;
    case 5:// vertical flip + 90 rotate right
      // ctx0.rotate(0.5 * Math.PI);
      // ctx0.scale(1, -1);
      ctx1.rotate(0.5 * Math.PI);
      ctx1.scale(1, -1);
      break;
    case 6:// 90 rotate right
      // ctx0.rotate(0.5 * Math.PI);
      // ctx0.translate(0, -cHeight);
      ctx1.rotate(0.5 * Math.PI);
      ctx1.translate(0, -wHeight);
      break;
    case 7:// horizontal flip + 90 rotate right
      // ctx0.rotate(0.5 * Math.PI);
      // ctx0.translate(cWidth, -cHeight);
      // ctx0.scale(-1, 1);
      ctx1.rotate(0.5 * Math.PI);
      ctx1.translate(wWidth, -wHeight);
      ctx1.scale(-1, 1);
      break;
    case 8:// 90 rotate left
      // ctx0.rotate(-0.5 * Math.PI);
      // ctx0.translate(-cWidth, 0);
      ctx1.rotate(-0.5 * Math.PI);
      ctx1.translate(-wWidth, 0);
      break;
    default:
      break;
  }
  // ctx0.drawImage(img, 0, 0, cWidth, cHeight); // canvas0 の方の一連の処理は 不用？
  ctx1.drawImage(img, 0, 0, wWidth, wHeight); 

  // cWidth = canvas0.width;
  // cHeight = canvas0.height;
  // wWidth = canvas1.width;
  // wHeight = canvas1.height;
  // console.log(orient, wWidth, wHeight);
  // var imgData = ctx1.getImageData(0, 0, wWidth, wHeight);

  // ctrl.quality(imgData.data, wWidth, wHeight); // 不用？
  // return {canvasO: canvas, canvasT:canvas1, size: tranSize};
  return {canvasO: null, canvasT:canvas1, size: tranSize};
}  // transformOrient(img, lsize) {

