/*
ゴミ取り処理
boxサイズの窓の周囲がbcと同じだったら、そのboxの中をbcとするる
*/
//  ada_thresh10.jsから
function gomiRemove(image, box, w, h, bc) {
  var nb = (box - 1) * 4;
  for (var i = 0; i < h - box; i++) {
    for (var j = 0; j < w - box; j++) {
      var n = 0;

        var ki = 0;
        for (var kj = 0; kj < box; kj++) {
          var m = (i + ki) * w + (j + kj);
          if (image[m] == bc) n++;
          else n = -1;
        }
      if (n != -1) {
        var ki = box - 1;
        for (var kj = 0; kj < box; kj++) {
          var m = (i + ki) * w + (j + kj);
          if (image[m] == bc) n++;
          else n = -1;
        }
      }
      if (n != -1) {
        var kj = 0;
        for (var ki = 1; ki < box - 1; ki++) {
          var m = (i + ki) * w + (j + kj);
          if (image[m] == bc) n++;
          else n = -1;
        }
      }
      if (n != -1) {
        var kj = box - 1;
        for (var ki = 1; ki < box - 1; ki++) {
          var m = (i + ki) * w + (j + kj);
          if (image[m] == bc) n++;
          else n = -1;
        }
      }

      if (n == nb) {
        for (var ki = 1; ki < box - 1; ki++) {
          for (var kj = 1; kj < box - 1; kj++) {
            var m = (i + ki) * w + (j + kj);
            image[m] = bc;
          }
        }
      }
    }
  }
  return image;
}
//  ada_thresh10.jsから
function gomiRemoveData(boxsize, data, width, height, color) {
//console.log("dilation " + width + " " + height);
  var img = new Array(width * height);
  for (var i = 0; i < data.data.length; i += 4) img[i / 4] = data.data[i];

  img = gomiRemove(img, boxsize, width, height, 255);

  for (var i = 0; i < img.length; i++) {
    data.data[4 * i] = img[i];
    data.data[4 * i + 1] = img[i];
    data.data[4 * i + 2] = img[i];
  }
  return data;
}

//膨張処理
//  ada_thresh10.jsから
function dilationData(ne, data, width, height, color) {
  //console.log("dilation " + width + " " + height);
    var img = new Array(width * height);
    for (var i = 0; i < data.data.length; i += 4) img[i / 4] = data.data[i];
  
    for (var i = 0; i < ne; i++) img = dilate(img, width, height, 0, 255);
  
    for (var i = 0; i < img.length; i++) {
      data.data[4 * i] = img[i];
      data.data[4 * i + 1] = img[i];
      data.data[4 * i + 2] = img[i];
    }
    return data;
}

/*
  Efficiently Implementing Dilate and Erode Image Functions
    https://blog.ostermiller.org/dilate-and-erode
*/
//  ada_thresh10.jsから
// Best dilate by one solution
// tc: target color,  bc: background color
function dilate(image, w, h, tc, bc) {
  for (var i = 0; i < h; i++) {
    for (var j = 0; j < w; j++) {
      var k = i * w + j;
      if (image[k] == tc) {
        if (i     > 0 && image[k - w] == bc) image[k - w] = tc + 1;
        if (j     > 0 && image[k - 1] == bc) image[k - 1] = tc + 1;
        if (i + 1 < h && image[k + w] == bc) image[k + w] = tc + 1;
        if (j + 1 < w && image[k + 1] == bc) image[k + 1] = tc + 1;
      }
    }
  }
  for (var i = 0; i < h; i++) {
    for (var j = 0; j < w; j++) {
      var k = i * w + j;
      if (image[k] == tc + 1) image[k] = tc;
    }
  }
  return image;
}

  
