/*
  線分群から求めた最大矩形に対して、
  mlongside: 矩形変換後の長辺の大きさ
*/
function findRectPath(data, WIDTH, HEIGHT, sV, mlongside) {
  // var L = 40;

//対象が入る四角形を求める
  var x1 = WIDTH, x2 = -WIDTH, y1 = HEIGHT, y2 = -HEIGHT;
  for (var i = 0; i < sV.length; i++) {
    if (sV[i] == null) continue;
    if (x1 > data[sV[i]].x1) x1 = data[sV[i]].x1;
    if (y1 > data[sV[i]].y1) y1 = data[sV[i]].y1;
    if (x1 > data[sV[i]].x2) x1 = data[sV[i]].x2;
    if (y1 > data[sV[i]].y2) y1 = data[sV[i]].y2;

    if (x2 < data[sV[i]].x1) x2 = data[sV[i]].x1;
    if (y2 < data[sV[i]].y1) y2 = data[sV[i]].y1;
    if (x2 < data[sV[i]].x2) x2 = data[sV[i]].x2;
    if (y2 < data[sV[i]].y2) y2 = data[sV[i]].y2;
  }
  var width = x2 - x1;
  var height = y2 - y1;
  var wp = width / 16;
  var hp = height / 16;
//console.log('base rect', x1, y1, x2, y2, width, height);

/*
矩形の中心から、上方向、右方向、下方向、左方向に向かって、矩形を構成しているらしい線分候補を求める。
　候補となる線分は、中心から外郭への線分とLSDで求めた線分の交点によって候補を決める。
　候補条件
　・LSD線分が、外郭に接していないこと。
　・中心から外郭への線分とLSD線分との交点が、中心と外郭の間にあること。
*/
//up side
  var cS = {x: width / 2 + x1, y: height / 2 + y1};
  var cE = {x: width / 2 + x1, y: y1};//cEはセンターから終点
  var uP = new Array();
  for (var i = 0; i < sV.length; i++) {
    if (sV[i] == null) continue;
    // if (data[sV[i]].dist < L) continue; 
    var pointA = {x: data[sV[i]].x1, y: data[sV[i]].y1};
    var pointB = {x: data[sV[i]].x2, y: data[sV[i]].y2};
//    if (pointA.x == 0 || pointA.y == 0 || pointB.x == 0 || pointB.y == 0) continue;
//    if (pointA.x == WIDTH - 1 || pointB.x == WIDTH - 1) continue;
    if (pointA.y > cS.y || pointB.y > cS.y) continue;
    var c = ctrl.CalcIntersectionPoint(pointA, pointB, cS, cE);
    if (c.ang == undefined) continue;//平行
    if (c.ang < 45 || c.ang > 135) continue;
    if (c.y > cS.y - hp|| c.y < 0) continue;
    var abCen = {};//線分ＡＢの重心
    abCen.x = (pointA.x + pointB.x) / 2;
    abCen.y = (pointA.y + pointB.y) / 2;
//abHは中心からの高さ方向の距離
//abWは中心からの横方向の距離
    var abW = cS.x - abCen.x >= 0 ? cS.x - abCen.x : abCen.x - cS.x;
    if (abW < 100) abW = 100;
    var dist = data[sV[i]].dist;
    var abH = cS.y - abCen.y;
    var force = dist * abH / Math.ceil(abW / 100);//forceは、中心から遠くて、線分が長いことを意味する
//console.log('u', sV[i], Math.round(c.ang), Math.round(data[sV[i]].dist), Math.round(force));
    uP.push({c: c, n: sV[i], ang: c.ang, dist: dist, abCen: abCen, abH: abH, abW: abW, force: force});
  }
  uP.keySort({force: "desc"});//長い線分で、中心から遠いこと

//reight side
  cE= {x: WIDTH, y: height / 2 + y1};
  var uR = new Array();
  for (var i = 0; i < sV.length; i++) {
    if (sV[i] == null) continue;
    // if (data[sV[i]].dist < L) continue;
    var pointA = {x: data[sV[i]].x1, y: data[sV[i]].y1};
    var pointB = {x: data[sV[i]].x2, y: data[sV[i]].y2};
//    if (pointA.x == 0 || pointA.y == 0 || pointB.x == 0 || pointB.y == 0) continue;
//    if (pointA.y == HEIGHT - 1 || pointB.y == HEIGHT - 1) continue;
    if (pointA.x < cS.x || pointB.x < cS.x) continue;
    var c = ctrl.CalcIntersectionPoint(pointA, pointB, cS, cE);
    if (c.ang == undefined) continue;//平行
    if (c.ang < 45 || c.ang > 135) continue;
    if (c.x >= WIDTH || c.x < cS.x + wp) continue;
    var abCen = {};//線分ＡＢの重心
    abCen.x = (pointA.x + pointB.x) / 2;
    abCen.y = (pointA.y + pointB.y) / 2;
    var abW = cS.y - abCen.y >= 0 ? cS.y - abCen.y : abCen.y - cS.y;
    if (abW < 100) abW = 100;
    var dist = data[sV[i]].dist;
    var abH = abCen.x- cS.x;
    var force = dist * abH / Math.ceil(abW / 100);//forceは、中心から遠くて、線分が長いことを意味する
//console.log('r', sV[i], Math.round(c.ang), Math.round(data[sV[i]].dist), Math.round(force));
    uR.push({c: c, n: sV[i], ang: c.ang, dist: dist, abCen: abCen, abH: abH, abW: abW, force: force});
  }
  uR.keySort({force: "desc"});//長い線分で、中心から遠いこと

//down side
  cE = {x: width / 2 + x1, y: HEIGHT};
  var uD = new Array();
  for (var i = 0; i < sV.length; i++) {
    if (sV[i] == null) continue;
    // if (data[sV[i]].dist < L) continue;
    var pointA = {x: data[sV[i]].x1, y: data[sV[i]].y1};
    var pointB = {x: data[sV[i]].x2, y: data[sV[i]].y2};
//    if (pointA.x == 0 || pointA.y == 0 || pointB.x == 0 || pointB.y == 0) continue;
//    if (pointA.x == WIDTH - 1 || pointB.x == WIDTH - 1) continue;
    if (pointA.y < cS.y || pointB.y < cS.y) continue;
    var c = ctrl.CalcIntersectionPoint(pointA, pointB, cS, cE);
    if (c.ang == undefined) continue;//平行
    if (c.ang < 45 || c.ang > 135) continue;
    if (c.y >= HEIGHT || c.y < cS.y + hp) continue;
    var abCen = {};//線分ＡＢの重心
    abCen.x = (pointA.x + pointB.x) / 2;
    abCen.y = (pointA.y + pointB.y) / 2;
    var abW = cS.x - abCen.x >= 0 ? cS.x - abCen.x : abCen.x - cS.x;
    if (abW < 100) abW = 100;
    var dist = data[sV[i]].dist;
    var abH = abCen.y- cS.y;
    var force = dist * abH / Math.ceil(abW / 100);//forceは、中心から遠くて、線分が長いことを意味する
//console.log('d', sV[i], Math.round(c.ang), Math.round(data[sV[i]].dist), Math.round(force));
    uD.push({c: c, n: sV[i], ang: c.ang, dist: dist, abCen: abCen, abH: abH, abW: abW, force: force});
  }
  uD.keySort({force: "desc"});//長い線分で、中心から遠いこと

//left side
  cE = {x: x1, y: height / 2 + y1};
  var uL = new Array();
  for (var i = 0; i < sV.length; i++) {
    if (sV[i] == null) continue;
    // if (data[sV[i]].dist < L) continue;
    var pointA = {x: data[sV[i]].x1, y: data[sV[i]].y1};
    var pointB = {x: data[sV[i]].x2, y: data[sV[i]].y2};
//    if (pointA.x == 0 || pointA.y == 0 || pointB.x == 0 || pointB.y == 0) continue;
//    if (pointA.y == HEIGHT - 1 || pointB.y == HEIGHT - 1) continue;
    if (pointA.x > cS.x || pointB.x > cS.x) continue;
    var c = ctrl.CalcIntersectionPoint(pointA, pointB, cS, cE);
    if (c.ang == undefined) continue;//平行
    if (c.ang < 45 || c.ang > 135) continue;
    if (c.x > cS.x - wp || c.x < 0) continue;
    var abCen = {};//線分ＡＢの重心
    abCen.x = (pointA.x + pointB.x) / 2;
    abCen.y = (pointA.y + pointB.y) / 2;
    var abW = cS.y - abCen.y >= 0 ? cS.y - abCen.y : abCen.y - cS.y;
    if (abW < 100) abW = 100;
    var dist = data[sV[i]].dist;
    var abH = cS.x - abCen.x;
    var force = dist * abH / Math.ceil(abW / 100);//forceは、中心から遠くて、線分が長いことを意味する
//console.log('l', sV[i], Math.round(c.ang), Math.round(data[sV[i]].dist), Math.round(force));
    uL.push({c: c, n: sV[i], ang: c.ang, dist: dist, abCen: abCen, abH: abH, abW: abW, force: force});
  }
  uL.keySort({force: "desc"});//長い線分で、中心から遠いこと

//console.log('len', uP.length, uR.length, uD.length, uL.length);

  // let rect1canv = vecDataDisp0('rect1', [uP, uR, uD, uL], data, cS, width, height, WIDTH, HEIGHT);

  if (uP.length == 0) return null;
  if (uP.length > 1) {
    for (var i = uP.length - 1; i > 0; i--) {
      if (uP[0].force > uP[i].force * 8) uP.splice(i, 1);
    }
    if (uP.length > 1) {
      uP.keySort({dist: "desc"});
      for (var i = uP.length - 1; i > 0; i--) {
        if (uP[0].dist > uP[i].dist * 5) uP.splice(i, 1);
      }
    }
  }
//console.log('uP', JSON.stringify(uP));
  if (uR.length == 0) return null;
  if (uR.length > 1) {
    for (var i = uR.length - 1; i > 0; i--) {
      if (uR[0].force > uR[i].force * 8) uR.splice(i, 1);
    }
    if (uR.length > 1) {
      uR.keySort({dist: "desc"});
      for (var i = uR.length - 1; i > 0; i--) {
        if (uR[0].dist > uR[i].dist * 5) uR.splice(i, 1);
      }
    }
  }
//console.log('uR', JSON.stringify(uR));
  if (uD.length == 0) return null;
  if (uD.length > 1) {
    for (var i = uD.length - 1; i > 0; i--) {
      if (uD[0].force > uD[i].force * 8) uD.splice(i, 1);
    }
    if (uD.length > 1) {
      uD.keySort({dist: "desc"});
      for (var i = uD.length - 1; i > 0; i--) {
        if (uD[0].dist > uD[i].dist * 5) uD.splice(i, 1);
      }
    }
  }
//console.log('uD', JSON.stringify(uD));
  if (uL.length == 0) return null;
  if (uL.length > 1) {
    for (var i = uL.length - 1; i > 0; i--) {
      if (uL[0].force > uL[i].force * 8) uL.splice(i, 1);
    }
    if (uL.length > 1) {
      uL.keySort({dist: "desc"});
      for (var i = uL.length - 1; i > 0; i--) {
        if (uL[0].dist > uL[i].dist * 5) uL.splice(i, 1);
      }
    }
  }
//console.log('uL', JSON.stringify(uL));

//console.log('len', uP.length, uR.length, uD.length, uL.length);

  // let rect2canv = vecDataDisp0('rect2', [uP, uR, uD, uL], data, cS, width, height, WIDTH, HEIGHT);

  var re = [];

  if (uP.length == 1) re.push({v: uP[0].n, f: 'uP'});
  if (uR.length == 1) re.push({v: uR[0].n, f: 'uR'});
  if (uD.length == 1) re.push({v: uD[0].n, f: 'uD'});
  if (uL.length == 1) re.push({v: uL[0].n, f: 'uL'});

  re = crossCheck(uP, uR, uD, uL, re, data, width, height);

//console.log('re', JSON.stringify(re));
  var rec = [];
  for (var i = 0; i < re.length; i++) {
    set_addData(re[i], rec);
  }
//console.log('rec', rec.length, JSON.stringify(rec));

  if (rec.length < 4) return {rect: re};

//候補になった線分について、一番外側の線分を求める
  var pP = [{f: 'uP', data: uP, ksort: {y: 'asc'}}, {f: 'uR', data: uR, ksort: {x: 'desc'}}, {f: 'uD', data: uD, ksort: {y: 'desc'}}, {f: 'uL', data: uL, ksort: {x: 'asc'}}];
  var rr = [];
  var rect = [];
  for (var k = 0; k < 4; k++) {
    rr[k] = [];
    for (var i = 0; i < rec.length; i++) {
      if (rec[i].f == pP[k].f) {
        for (var j = 0; j < pP[k].data.length; j++) {
          if (pP[k].data[j].n == rec[i].v) {
            rr[k].push({v: rec[i].v, x: pP[k].data[j].c.x, y: pP[k].data[j].c.y, ksort: pP[k].ksort});
            break;
          }
        }
      }
    }
    if (rr[k].length > 1) {
//console.log(rr[k].length, rr[k][0].ksort);
      rr[k].keySort(rr[k][0].ksort);
    }
    rect[k] = {v: rr[k][0].v, f: pP[k].f};
  }
//console.log('rr', JSON.stringify(rr));
//console.log('rect', JSON.stringify(rect));

//4辺から角（コーナー）を求める
  if (rect.length == 4) {
    var r = new Array(4);
    for (var i = 0; i < 4; i++) r[i] = null;
    for (var i = 0; i <rect.length; i++) {
      if (rect[i].f == 'uP') r[0] = rect[i].v;
      else if (rect[i].f == 'uR') r[1] = rect[i].v;
      else if (rect[i].f == 'uD') r[2] = rect[i].v;
      else if (rect[i].f == 'uL') r[3] = rect[i].v;
    }
    for (var i = 0; i < 4; i++) if (r[i] == null) return {rect: rect};
//console.log('r', r);

    var pp = new Array(4);
    var pointA = {x: data[r[0]].x1, y: data[r[0]].y1};
    var pointB = {x: data[r[0]].x2, y: data[r[0]].y2};
    var pointC = {x: data[r[1]].x1, y: data[r[1]].y1};
    var pointD = {x: data[r[1]].x2, y: data[r[1]].y2};
    var c = ctrl.CalcIntersectionPoint(pointA, pointB, pointC, pointD);
    pp[1] = [];
    pp[1][0] = c.x;
    pp[1][1] = c.y;

    pointA = {x: data[r[2]].x1, y: data[r[2]].y1};
    pointB = {x: data[r[2]].x2, y: data[r[2]].y2};
    c = ctrl.CalcIntersectionPoint(pointC, pointD, pointA, pointB);
    pp[2] = [];
    pp[2][0] = c.x;
    pp[2][1] = c.y;

    pointC = {x: data[r[3]].x1, y: data[r[3]].y1};
    pointD = {x: data[r[3]].x2, y: data[r[3]].y2};
    c = ctrl.CalcIntersectionPoint(pointA, pointB, pointC, pointD);
    pp[3] = [];
    pp[3][0] = c.x;
    pp[3][1] = c.y;

    pointA = {x: data[r[0]].x1, y: data[r[0]].y1};
    pointB = {x: data[r[0]].x2, y: data[r[0]].y2};
    c = ctrl.CalcIntersectionPoint(pointC, pointD, pointA, pointB);
    pp[0] = [];
    pp[0][0] = c.x;
    pp[0][1] = c.y;
//console.log('pp', pp);
//アスペクト比を求める
    var rW = Math.sqrt((pp[0][0] - pp[1][0]) * (pp[0][0] - pp[1][0]));
    var rH = Math.sqrt((pp[0][1] - pp[3][1]) * (pp[0][1] - pp[3][1]));
    var whRatio = getRealAspectRatio(pp[0], pp[1], pp[2], pp[3]);
    if (!whRatio || whRatio == Infinity) return null;
console.log('whRatio', whRatio, Math.round(rW), Math.round(rH));
    if (rW > rH) {
      rW = mlongside;
      rH = Math.round(mlongside / whRatio);
    }
    else {
      rW = Math.round(mlongside * whRatio);
      rH = mlongside;
    }
console.log(rW, rH);
    if (rW == 0 || rH == 0) return {rect: rect};
    return {pp: pp, rW: rW, rH: rH, r: r, rect: rect};
  }
  else return {rect: rect};
} // function findRectPath(data, WIDTH, HEIGHT, sV) 

