function set_addData(v, data) {
  var fl = false;
  for (var i = 0; i < data.length; i++) {
    if (data[i].v == v.v) {
      if (data[i].dist < v.dist) data[i].dist = v.dist;
      fl = true;
      break;
    }
  }
  if (fl) return;
  else {
    data.push({v: v.v, f: v.f, dist: v.dist});
  }
}

/*
  Calculating aspect ratio of Perspective Transform destination image
    https://code.i-harness.com/en/q/2482fad
  proportions of a perspective-deformed rectangle
    https://stackoverflow.com/questions/1194352/proportions-of-a-perspective-deformed-rectangle/1222855#1222855
*/
function getRealAspectRatio(p0, p1, p2, p3) {
  var u0 = 400;
  var v0 = 300;

  var m1x = p0[0] - u0;
  var m1y = p0[1] - v0;
  var m2x = p1[0] - u0;
  var m2y = p1[1] - v0;
  var m3x = p3[0] - u0;
  var m3y = p3[1] - v0;
  var m4x = p2[0] - u0;
  var m4y = p2[1] - v0;

  var k2 = ((m1y - m4y) * m3x - (m1x - m4x) * m3y + m1x * m4y - m1y * m4x) / ((m2y - m4y) * m3x - (m2x - m4x) * m3y + m2x * m4y - m2y * m4x);

  var k3 = ((m1y - m4y) * m2x - (m1x - m4x) * m2y + m1x * m4y - m1y * m4x) / ((m3y - m4y) * m2x - (m3x - m4x) * m2y + m3x * m4y - m3y * m4x);
//console.log(k2, k3);
  k2 = Math.round(k2);
  k3 = Math.round(k3);
//console.log(k2, k3);

  var f_squared = -((k3 * m3y - m1y) * (k2 * m2y - m1y) + (k3 * m3x - m1x) * (k2 * m2x - m1x)) / ((k3 - 1) * (k2 - 1));
//console.log('f_squared', f_squared);

  var whRatio = Math.sqrt(
    (Math.pow((k2 - 1), 2) +
     Math.pow((k2 * m2y - m1y), 2) / f_squared +
     Math.pow((k2 * m2x - m1x), 2) / f_squared) / (Math.pow((k3 - 1), 2) +
     Math.pow((k3 * m3y - m1y), 2) / f_squared +
     Math.pow((k3 * m3x - m1x), 2) / f_squared));
//console.log('whRatio0', whRatio);

  if (k2 == 1 && k3 == 1) {
    whRatio = Math.sqrt((Math.pow((m2y - m1y), 2) + Math.pow((m2x - m1x), 2)) / (Math.pow((m3y - m1y), 2) + Math.pow((m3x - m1x), 2)));
  }
//console.log('whRatio1', whRatio);
  return whRatio;
}

/*
  How do you sort an array on multiple columns?
    https://stackoverflow.com/questions/2784230/how-do-you-sort-an-array-on-multiple-columns
*/
Array.prototype.keySort = function(keys) {

  keys = keys || {};

  // via
  // http://stackoverflow.com/questions/5223/length-of-javascript-object-ie-associative-array
  var obLen = function(obj) {
    var size = 0, key;
    for (key in obj) {
      if (obj.hasOwnProperty(key))
        size++;
    }
    return size;
  };

  // avoiding using Object.keys because i guess did it have IE8 issues?
  // else var obIx = fucntion(obj, ix){ return Object.keys(obj)[ix]; } or
  // whatever
  var obIx = function(obj, ix) {
    var size = 0, key;
    for (key in obj) {
      if (obj.hasOwnProperty(key)) {
        if (size == ix)
          return key;
        size++;
      }
    }
    return false;
  };

  var keySort = function(a, b, d) {
    d = d !== null ? d : 1;
    // a = a.toLowerCase(); // this breaks numbers
    // b = b.toLowerCase();
    if (a == b)
      return 0;
    return a > b ? 1 * d : -1 * d;
  };

  var KL = obLen(keys);

  if (!KL) return this.sort(keySort);

  for (var k in keys) {
    // asc unless desc or skip
    keys[k] =
      keys[k] == 'desc' || keys[k] == -1  ? -1 : (keys[k] == 'skip' || keys[k] === 0 ? 0 : 1);
  }

  this.sort(function(a, b) {
    var sorted = 0, ix = 0;

    while (sorted === 0 && ix < KL) {
      var k = obIx(keys, ix);
      if (k) {
        var dir = keys[k];
        sorted = keySort(a[k], b[k], dir);
        ix++;
      }
    }
    return sorted;
  });
  return this;
};

