var Points;
var compFunc;

//  colored : 領域内と認識した画素の色付け
function floodfill(imagedata, x, y, colored, width, height, _compFunc, setFunc) {
  var data = imagedata.data;
  compFunc = _compFunc;
  Points = [];
  var length = data.length;
  var Q = [];
  var i = (x + y * width) * 4;
  var e = i, w = i, me, mw, w2 = width * 4;

  var targetcolor = [data[i], data[i + 1], data[i + 2], data[i + 3]];//塗りつぶしの内側の画素
//console.log(x + ' ' + y + ' ' + colored + ' ' + width + ' ' + height + ' ' + targetcolor);

  if (!compFunc(i, targetcolor, colored, data, length)) { return false; }
  Q.push(i);
  while (Q.length) {
    i = Q.pop();
//console.log(i % width / 4 + ' ' + parseInt(i / width) / 4);
    if (setFunc(i, targetcolor, colored, data, length, compFunc)) {
      e = i;
      w = i;
      mw = parseInt(i / w2) * w2; //left bound
      me = mw + w2;               //right bound
//go left until edge hit
      while (mw < w && mw < (w -= 4) && setFunc(w, targetcolor, colored, data, length, compFunc));
//go right until edge hit
      while (me > e && me > (e += 4) && setFunc(e, targetcolor, colored, data, length, compFunc));

      for (var j = w; j < e; j += 4) {
//queue y-1
        if (j - w2 >= 0     && compFunc(j - w2, targetcolor, colored, data, length))
          Q.push(j - w2);
//queue y+1
        if (j + w2 < length && compFunc(j + w2, targetcolor, colored, data, length))
          Q.push(j + w2);
      }
    }
  }
  for (var i = 0; i < length; i += 4) {
    imagedata.data[i + 3] = 255;
//    if (imagedata.data[i + 3] == colored) imagedata.data[i + 3] = 255;
  }
  return data;
}

/*
  画素iがtargetと同じ色であるか、判定を行う
  戻り値がtrueの場合、targetと同じで内側を示し、falseの場合、境界に来たことを示す
*/
function fillCompFunc0(i, targetcolor, colored, data, length) {
  if (i < 0 || i >= length) return false; //out of bounds

//targetと現在の画素の色が同じ場合
  if (data[i + 3] == colored) {
    return false;
  }
  else {
    if (
      targetcolor[0] == data[i    ] &&
      targetcolor[1] == data[i + 1] &&
      targetcolor[2] == data[i + 2]
    ) return true; //target to surface within tolerance
  }

  Points.push(i);

  return false; //no match
};

//境界判定を行う
function fillCompFunc(i, targetcolor, colored, data, length) {
  if (i < 0 || i >= length) return false; //out of bounds

  if (data[i + 3] == colored) return false; //surface is invisible and fill is visible
//境界色まで色を塗る
  else {
    if (
      data[i    ] != 1 ||
      data[i + 1] != 1 ||
      data[i + 2] != 1
    ) {
//console.log(i);
    return true;
    }
//else console.log(i);

//    if (data[i + 3] == colored) return true;
  }
  Points.push(i);
  return false; //no match
}

/*
  floodfillでチェックした点を色分けする
*/
//function fillSetFunc(i, targetcolor, colored, data, length, fillCompFunc) {
function fillSetFunc(i, targetcolor, colored, data, length) {
//  if (fillCompFunc(i, targetcolor, colored, data, length)) {
  if (compFunc(i, targetcolor, colored, data, length)) {
//console.log(colored, data[i + 3], i % width / 4 + ' ' + parseInt(i / width) / 4);
    //fill the color
    data[i + 0] = 255;
    data[i + 1] = 255;
    data[i + 2] = 255;
    data[i + 3] = colored;
    return true;
  }
  return false;
}

