var floodfill0 = function (data, startX, startY, fillColor) {
  var startPos = (startY * width + startX) * 4;

//塗りつぶし対象領域の色
  var startColor = {
    r: data[startPos    ],
    g: data[startPos + 1],
    b: data[startPos + 2],
    a: data[startPos + 3]
  };
  var todo = [[startX, startY]];

var num = 0;
  while (todo.length) {
    var pos = todo.pop();
    var x = pos[0];
    var y = pos[1];
    var currentPos = (y * width + x) * 4;
console.log(x, y);
    while ((y-- >= 0) && matchStartColor(data, currentPos, startColor)) {
      currentPos -= width * 4;
//num++;if(num > 1000) return;
console.log(x, y, currentPos);
    }

    currentPos += width * 4;
    ++y;
    var reachLeft = false;
    var reachRight = false;

    while ((y++ < height - 1) && matchStartColor(data, currentPos, startColor)) {
console.log(x, y, currentPos);
//num++;if(num > 1000) return;

      colorPixel(data, currentPos, fillColor);

      if (x > 0) {
        if (matchStartColor(data, currentPos - 4, startColor)) {
          if (!reachLeft) {
            todo.push([x - 1, y]);
            reachLeft = true;
          }
        }
        else if (reachLeft) {
          reachLeft = false;
        }
      }

      if (x < width - 1) {
        if (matchStartColor(data, currentPos + 4, startColor)) {
          if (!reachRight) {
            todo.push([x + 1, y]);
            reachRight = true;
          }
        }
        else if (reachRight) {
          reachRight = false;
        }
      }

      currentPos += width * 4;
    }
  }

  return data;
};

var matchStartColor = function (data, pos, startColor) {
console.log(data[pos + 3]);
/*
  return (data[pos    ] == startColor.r &&
      data[pos + 1] == startColor.g &&
      data[pos + 2] == startColor.b &&
      data[pos + 3] == startColor.a);
*/
  return (data[pos + 3] != colored);
/*
  if (data[pos + 3] == colored) return false;
  else return true;
*/
};

var colorPixel = function (data, pos, color) {
  data[pos    ] = color.r || 0;
  data[pos + 1] = color.g || 0;
  data[pos + 2] = color.b || 0;
//  data[pos + 3] = colored;
};

