// histogram.jsから

function toHSV2(ImageData) {
  var w = ImageData.width;
  var h = ImageData.height;
  var data = ImageData.data;
  let Hdata = new Array(w * h);
  let Sdata = new Array(w * h);
  let Vdata = new Array(w * h);
  // let Odata = new Array(w * h);

  let n= 0;
  for (let i = 0; i < h; i++) {
    for (let j = 0; j < w; j++) {
      const k = (i * w + j) * 4;
//      hsv = RGBtoHSV360(data[k], data[k + 1], data[k + 2]);
      const hsv = RGBtoHSV255(data[k], data[k + 1], data[k + 2]);

      Hdata[n] = hsv[0];
      Sdata[n] = hsv[1];
      Vdata[n] = hsv[2];
      n++;
    }
  }
//  binarization("mono", Sdata, w, h, 20);
  // hsvDisp('hsvD', w, h);
  return { Hdata, Sdata, Vdata };
}

function RGBtoHSV255(r, g, b) {
  var h, // 0..360
    s, v, // 0..255
    max = Math.max(Math.max(r, g), b),
    min = Math.min(Math.min(r, g), b);

  // hue の計算
  if (max == min) {
    h = 0; // 本来は定義されないが、仮に0を代入
  } else if (max == r) {
    h = 60 * (g - b) / (max - min) + 0;
  } else if (max == g) {
    h = (60 * (b - r) / (max - min)) + 120;
  } else {
    h = (60 * (r - g) / (max - min)) + 240;
  }

  while (h < 0) {
    h += 360;
  }

  // saturation の計算
/*
  if (coneModel) {
    // 円錐モデルの場合
    s = max - min;
  } else {
*/
    s = (max == 0)
      ? 0 // 本来は定義されないが、仮に0を代入
      : (max - min) / max * 255;
//  }

  // value の計算
  v = max;

//  return {'h': h, 's': s, 'v': v};
  return [Math.floor(h / 360 * 255), s, v];
}

export {
  toHSV2
}