function checkEdge0(dmh, dhh, dms, dhs, dmv, dhv, width, height) {
	var dh = new Array();
	dh.push((dhh.row[0] + dhh.row[1]) / 2 - dhh.row[0]);
	dh.push((dhh.row[0] + dhh.row[1] + dhh.row[2]) / 3 - dhh.row[1]);
	for (var i = 2; i < dhh.row.length - 2; i++) {
		var d = 0;
		d += dhh.row[i - 2] * 3;
		d += dhh.row[i - 1];
		d += dhh.row[i + 1];
		d += dhh.row[i + 2] * 3;
		dh.push(d / 8 - dhh.row[i]);
	}
	var ds = new Array();
	ds.push((dhs.row[0] + dhs.row[1]) / 2 - dhs.row[0]);
	ds.push((dhs.row[0] + dhs.row[1] + dhs.row[2]) / 3 - dhs.row[1]);
	for (var i = 2; i < dhs.row.length - 2; i++) {
		var d = 0;
		d += dhs.row[i - 2] * 3;
		d += dhs.row[i - 1];
		d += dhs.row[i + 1];
		d += dhs.row[i + 2] * 3;
		ds.push(d / 8 - dhs.row[i]);
	}
	var dv = new Array();
	dv.push((dhv.row[0] + dhv.row[1]) / 2 - dhv.row[0]);
	dv.push((dhv.row[0] + dhv.row[1] + dhv.row[2]) / 3 - dhv.row[1]);
	for (var i = 2; i < dhv.row.length - 2; i++) {
		var d = 0;
		d += dhv.row[i - 2] * 3;
		d += dhv.row[i - 1];
		d += dhv.row[i + 1];
		d += dhv.row[i + 2] * 3;
		dv.push(d / 8 - dhv.row[i]);
	}
/*
console.log('h', dhh.row, dh);
console.log('s', dhs.row, ds);
console.log('v', dhv.row, dv);
*/
	return [dh, ds, dv];
}

