//dを10等分して、それぞれの平均などを求める
function getAverage(d) {
	var start = 0;
	var min = 99999, max = -1, av = 0, nav = 0;
	var nlen = d.length;
	var interval = 10;
	var k = Math.round(nlen / interval);
	var data = new Array();
	for (var i = start; i < nlen; i += k) {
		var dd = new Array();
		var ik = i + k;
		if (ik > nlen) ik = nlen;
		for (var j = i; j < ik; j++) dd.push(d[j]);
		var val = standard_deviation(dd);
		if (val.ave < min) min = val.ave;
		if (val.ave > max) max = val.ave;
		data.push(val);
		nav++;
		av += val.ave;
//console.log(i, JSON.stringify(val));
	}
	return {len: k, min: min, max: max, data: data, row: d, av: av / nav};
}

