function getAverage1(Mdata, step, start, nlen) {
	if (!nlen) nlen = Mdata.length;
//console.log('step', step, start, nlen);
//	var dd = Mdata[start];

	var dd = 0;
	for (var i = start; i < start + 6; i++) dd += Mdata[i];
	dd /= 6;

	for (var i = start; i < nlen; i++) {
		var dv = Math.abs(dd - Mdata[i + 1]);
//console.log('dv', i, dv, dd, Mdata[i + 1]);
		if (dv > 15) {
			return i;
		}
	}
	return null;
}

//移動平均との差分
function getAverage2(Mdata, step, start, nlen) {
	if (!nlen) nlen = Mdata.length;
//console.log('step', step, start, nlen);
	for (var i = start + 2; i < nlen; i++) {
		var d = 0;
		d += Mdata[i - 2] * 3;
		d += Mdata[i - 1];
		d += Mdata[i + 1];
		d += Mdata[i + 2] * 3
		d = Math.abs(d / 8 - Mdata[i]);
		if (d > 4) {
			return i;
		}
	}
	return null;
}

