//3値の最大値を求める
function max3c(dh, ds, dv) {
	var maxv = dh;
	var maxc = 'h';
	if (ds >= maxv) {
		maxv = ds;
		maxc = 's';
	}
	if (dv >= maxv) {
		maxv = dv;
		maxc = 'v';
	}
	return {val: maxv, ch: maxc};
}

function median3c(a, b, c) {
	if (a == b) {
		return {val: b, ch: 's'};
	}
	else if (b == c) {
		return {val: b, ch: 's'};
	}
	else if (c == a) {
		return {val: c, ch: 'v'};
	}

	if (!a) {
		if (!b) return {val: c, ch: 'v'};
		if (!c) return {val: b, ch: 's'};
		return {val: Math.floor((b + c) / 2), ch: 's'};
	}
	else if (!b) {
		if (!a) return {val: c, ch: 'v'};
		if (!c) return {val: b, ch: 's'};
		return {val: Math.floor((a + c) / 2), ch: 'v'};
	}
	else if (!c) {
		if (!a) return {val: b, ch: 's'};
		if (!b) return {val: b, ch: 's'};
		return {val: Math.floor((a + b) / 2), ch: 's'};
	}

	var min, max, med, ch;
	min = max = a;
	if (b < min) min = b;
	if (c < min) min = c;
	if (b > max) max = b;
	if (c > max) max = c;
	if (a != min && a != max) {med = a; ch = 'h';}
	if (b != min && b != max) {med = b; ch = 's';}
	if (c != min && c != max) {med = c; ch = 'v';}
	return {val: med, ch: ch};
}

/*
	3つの値を入力してその中央値を調べる
		https://detail.chiebukuro.yahoo.co.jp/qa/question_detail/q1155451301
*/
function median3(a, b, c) {
	if (a == b) {
		return a;
	}
	else if (b == c) {
		return b;
	}
	else if (c == a) {
		return c;
	}

	if (!a) {
		if (!b) return c;
		if (!c) return b;
		return Math.floor((b + c) / 2);
	}
	else if (!b) {
		if (!a) return c;
		if (!c) return a;
		return Math.floor((a + c) / 2);
	}
	else if (!c) {
		if (!a) return b;
		if (!b) return a;
		return Math.floor((a + b) / 2);
	}

	var min, max, med;
	min = max = a;
	if (b < min) min = b;
	if (c < min) min = c;
	if (b > max) max = b;
	if (c > max) max = c;
	if (a != min && a != max) med = a;
	if (b != min && b != max) med = b;
	if (c != min && c != max) med = c;
	return med;
}

/*
 * 平均を求める
 */
function average(data, len) {
//console.log('average', len);
	var sum = 0;
	for (var i = 0; i < len; i++) {
		sum = sum + data[i];
	}
	return (sum / len);
}

//画像（大きさwidth、height）中で、x、y、w、hの範囲で中央値を求める
this.getMedianData = function(g, x, y, w, h, width, height) {
//console.log("start getMedianData filter " + g.length, x, y, w, h);
	if (w < 3) w = 3;
	if (h < 3) h = 3;
	if (w > width - 1) w = width - 1;
	if (h > height - 1) h = height - 1;
	if (x + w > width - 1) w = width - x - 1;
	if (y + h > height - 1) h = height - y - 1;
	if (x == 0) x = 1;
	if (y == 0) y = 1;
//console.log(x, y, w, h, width, height);

	for (var i = y; i <y + h; i++) {
		var s = '';
		for (var j = x; j < x + w; j++) {
			var k = i * width + j;
//			s += i + ' ' + j + ':' + g[k] + ' ';
			s += g[k] + ' ';
		}
//console.log(i, s);
	}

	var data = new Array(w * h);
	var n = 0;

	for (var i = y; i <y + h; i++) {
		var k = i * width;
		var d = new Array(3);
		for (var j = 0; j < 3; j++) {
			d[j] = new Array(3);
			d[j][0] = g[k - width + j + x];
			d[j][1] = g[k         + j + x];
			d[j][2] = g[k + width + j + x];
//console.log(i, d[j][0] + "," + d[j][1] + "," + d[j][2]);
		}
		var m = v33_first(d);
		data[n++] = m;
//console.log(m);

		for (var j = x + 3; j < x + w + 2; j++) {
			d = new Array(3);
			d[0] = g[k - width + j];
			d[1] = g[k         + j];
			d[2] = g[k + width + j];
//console.log(j, d[0] + "," + d[1] + "," + d[2]);
			m = add_v33(d);
			data[n++] = m;
//console.log(m);
		}
	}
	return data;
}

var _y, _Y;
var posLast;
//最初の8近傍の中央値を求める
function v33_first(x) {
	_y = new Array(4);
	for (var i = 0; i < 3; i++) {
		_y[i] = v3(x[i][0], x[i][1], x[i][2]);
	}
	_y[3] = v3(_y[0][1].v, _y[1][1].v, _y[2][1].v);//3群の3値データの中央値をソートする
	posLast = 2;

	_Y = new Array(3);
	for (var i = 0; i < 3; i++) {//中央値に従い、3群のデータを小大順に並び替える
		_Y[i] = _y[_y[3][i].p];
	}

	var m = median();
	return m;
}

//8近傍の右側の3値を加える
function add_v33(x) {
	posLast++;
	if (posLast >= 3) posLast = 0;

	_y[posLast] = v3(x[0], x[1], x[2]);

	_y[3] = v3(_y[0][1].v, _y[1][1].v, _y[2][1].v);//3群の3値データの中央値をソートする

	_Y = new Array(3);
	for (var i = 0; i < 3; i++) {//中央値に従い、3群のデータを小大順に並び替える
		_Y[i] = _y[_y[3][i].p];
	}

	var m = median();
	return m;
}

//中央値判定
function median(){
	if (_Y[1][1].v >= _Y[2][0].v && _Y[1][1].v <= _Y[0][2].v) {//case1 y<=x<=z
		return _Y[1][1].v;//中央値が5番目
	}
	else if (_Y[1][1].v <= _Y[2][0].v && _Y[1][1].v >= _Y[0][2].v) {//case1 z<=x<=y
		return _Y[1][1].v;//中央値が5番目
	}
	else if (_Y[1][1].v < _Y[2][0].v && _Y[1][1].v < _Y[0][2].v) {//case2 x<y and x<z
		var X = v3(_Y[2][0].v, _Y[0][2].v, _Y[1][2].v);//_Y[1][1]は大きい方から6番目で、_Y[1][1]より大きい_Y[1][2]、_Y[2]の最小値と_Y[0]の最大値をソートして、その最小値が5番目となる
		return X[0].v;
	}
	else if (_Y[1][1].v > _Y[2][0].v && _Y[1][1].v > _Y[0][2].v) {//case3 x>y and x>z
		var X = v3(_Y[2][0].v, _Y[0][2].v, _Y[1][0].v);//_Y[1][1]は大きい方から4番目で、_Y[1][1]より小さい_Y[1][0]、_Y[2]の最小値と_Y[0]の最大値をソートして、その最大値が5番目となる
		return (X[2].v);
	}
}

//3値データをソートする
/*
a>b>c
a>c>b

b>a>c
b>c>a

c>a>b
c>b>a
*/
function v3(a, b, c) {
	var x = new Array(3);

	if (a > b) {
		if (a > c) {
			if (b > c) {
//	a>b>c
				x[2] = {v:a, p:0};
				x[1] = {v:b, p:1};
				x[0] = {v:c, p:2};
			}
			else {
//	a>c>b
				x[2] = {v:a, p:0};
				x[1] = {v:c, p:2};
				x[0] = {v:b, p:1};
			}
		}
		else {
//	c>a>b
				x[2] = {v:c, p:2};
				x[1] = {v:a, p:0};
				x[0] = {v:b, p:1};
		}
	}
	else {
		if (a > c) {
			if (b > c) {
//	b>a>c
				x[2] = {v:b, p:1};
				x[1] = {v:a, p:0};
				x[0] = {v:c, p:2};
			}
		}
		else {
			if (b > c) {
//	b>c>a
				x[2] = {v:b, p:1};
				x[1] = {v:c, p:2};
				x[0] = {v:a, p:0};
			}
			else {
//	c>b>a
				x[2] = {v:c, p:2};
				x[1] = {v:b, p:1};
				x[0] = {v:a, p:0};
			}
		}
	}
	return x;
}

this.medianBubble = function(g, width, height) {
console.log("start medianBubble filter " + g.length);
	var grid = new Array(width * height);

	for (var i = 1; i < height - 1; i++) {
		var k = i * width;
		var k1 = k + 1;
		var d = new Array(3);
		for (var n = 0; n < 3; n++) {
			d[n] = new Array(3);
			d[n][0] = g[k1 - width + n - 1];
			d[n][1] = g[k1         + n - 1];
			d[n][2] = g[k1 + width + n - 1];
		}
		posLast = 2;
		var m = bubbleSort(d);
		grid[k1] = m[4];

		for (var j = 2; j < width - 1; j++) {
			posLast++;
			if (posLast >= 3) posLast = 0;

			d[posLast][0] = g[k + j - width + 1];
			d[posLast][1] = g[k + j         + 1];
			d[posLast][2] = g[k + j + width + 1];
			m = bubbleSort(d);
			grid[k + j] = m[4];
		}
	}
	return grid;
}

/*
	Bubble sort algorithm in JavaScript
		http://codereview.stackexchange.com/questions/87869/bubble-sort-algorithm-in-javascript
*/
function bubbleSort(x) {
	var a = [];
	var n = 0;
	for (var i = 0; i < 3; i++){
		a[n++] = x[i][0];
		a[n++] = x[i][1];
		a[n++] = x[i][2];
	}

// Variables for BubbleSort
	var empty = [];
	var counter = a.length;
	var last = 0;
	var secondCounter = 0;

// Variables for reversing

	var reverseCount = 0;
	var secondReverseCount = a.length - 1;

// Bubble Sort

	while (secondCounter < a.length){
		for (var i = 1; i < a.length; i++) {
			if (a[i -1] > a[i]) {
				var temp = a[i-1];
				a[i-1] = a[i];
				a[i] = temp;
			}
		}
		last = a[counter - 1];
		empty[secondCounter++] = last;
		counter--;
	}

// We have to reverse it now

	while (reverseCount < secondReverseCount) {
		var temp2 = empty[reverseCount];
		empty[reverseCount] = empty[secondReverseCount];
		empty[secondReverseCount] = temp2;

		reverseCount++;
		secondReverseCount--;
	}

	return empty;
}

this.laplacian = function(g, width, height) {
console.log("start laplacian filter " + g.length);
	var grid = new Buffer(width * height);
var n = 0;
	for (var i = 4; i < height - 4; i++) {
		var k = i * width;
		for (var j = 4; j < width - 4; j++) {
			var m = 
				- g[k - 4 * width + j    ] * 1
				- g[k - 3 * width + j    ] * 1
				- g[k - 2 * width + j    ] * 1
				- g[k - 1 * width + j    ] * 1

				- g[k             + j - 4] * 1
				- g[k             + j - 3] * 1
				- g[k             + j - 2] * 1
				- g[k             + j - 1] * 1
				+ g[k             + j    ] * 16
				- g[k             + j + 1] * 1
				- g[k             + j + 2] * 1
				- g[k             + j + 3] * 1
				- g[k             + j + 4] * 1

				- g[k + 1 * width + j    ] * 1
				- g[k + 2 * width + j    ] * 1
				- g[k + 3 * width + j    ] * 1
				- g[k + 4 * width + j    ] * 1;
			if (m >= -50) m = 0xff;
			else m = 0;

/*console.log(k + " " + i + " " + j + " " + g[k + j] + " " + m);
n++;
if (n > 10) break;
*/
			grid[k + j] = m;
		}
//break;
	}
	return grid;
}

this.unsharp_masking = function(g, width, height) {
console.log("start laplacian filter " + g.length);
	var grid = new Buffer(width * height);
var n = 0;
	var K = 1;
	for (var i = 2; i < height - 2; i++) {
		var k = i * width;
		for (var j = 2; j < width - 2; j++) {
			var m = 
				- g[k - 2 * width + j - 2] * K
				- g[k - 2 * width + j - 1] * K
				- g[k - 2 * width + j    ] * K
				- g[k - 2 * width + j + 1] * K
				- g[k - 2 * width + j + 2] * K

				- g[k - 1 * width + j - 2] * K
				- g[k - 1 * width + j - 1] * K
				- g[k - 1 * width + j    ] * K
				- g[k - 1 * width + j + 1] * K
				- g[k - 1 * width + j + 2] * K

				- g[k             + j - 2] * K
				- g[k             + j - 1] * K
				+ g[k             + j    ] * (24 * K + 25)
				- g[k             + j + 1] * K
				- g[k             + j + 2] * K

				- g[k + 1 * width + j - 2] * K
				- g[k + 1 * width + j - 1] * K
				- g[k + 1 * width + j    ] * K
				- g[k + 1 * width + j + 1] * K
				- g[k + 1 * width + j + 2] * K

				- g[k + 2 * width + j - 2] * K
				- g[k + 2 * width + j - 1] * K
				- g[k + 2 * width + j    ] * K
				- g[k + 2 * width + j + 1] * K
				- g[k + 2 * width + j + 2] * K;
			m /= 25;
			var M = m;
			if (M >= 255) M = 0xff;
			else M = 0;

			grid[k + j] = M;
		}
	}
	return grid;
}

